


.frame{
position: relative;
width: 100%;
height: 100%;
background-color: white;
overflow: hidden;
perspective: 400px;
.red-bg{
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  background: radial-gradient(55.76% 55.76% at 50% 50%, rgba(0, 0, 0, 0) 77.6%, rgba(0, 0, 0, 0.08) 100%), linear-gradient(179.13deg, #CB0000 0.75%, rgba(203, 0, 0, 0.63) 24.21%, rgba(255, 255, 255, 0) 76.17%);
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  div{
    display: none;
    position: absolute;
    width: 20vw;
    height: 20vw;
    border-radius: 50%;
    background-color: black;
    animation: scale 10s;
  }
} 

    
.floor{
    position: relative;
    transform: rotateX(30deg) translateZ(200px);
    transform-origin: 50% 50%;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    .zigzag-wrapper {
        position: absolute;
        width: 100%;
        animation: floor-move 100s linear infinite;
        .zigzag {
            min-width: 1980px;
        }      
              


      }
  
  }
}

  
@media (max-width: 600px) {
  .frame{
    perspective: none;
  }
  .floor{
    transform: rotateX(0deg) translateZ(0px);
    //transform-origin: 200% 150%;
  }
  .zigzag-wrapper{
    left: -120%;
    animation: floor-move 100s linear infinite;
  }
  .zigzag {
    width: 100%;
    height: 100px;
  }
    
} 



@keyframes floor-move {
  0% {
    transform: translateY(-90%);
  }
  100% {
    transform: translateY(0%);
  }
};

@keyframes scale{
  0%{
    transform: scale(0%);
    background-color: transparent;
  }
  100%{
    transform: scale(100%);
    background-color: black;
  }
}
          
@keyframes bg-color {
  0%{
    background-color: transparent;
  }
  100%{
    background-color: black;
  }
}

  
      
                
            
                
                
                
                
                    
                
                




